*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

button,
label {
  cursor: pointer;
}

input {
  font: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.timer {
  display: flex;

  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #212121;
  border-radius: 2px;
}

.value {
  margin-bottom: 15px;
  font-family: 'Roboto Mono', monospace;
  font-size: 80px;
  margin-top: 0;
  margin-bottom: 24px;
}

.label {
  font-family: 'Roboto Mono', monospace;
  font-size: 30px;
}
